import {
	mapState,
	mapMutations
} from 'vuex';
import {
	findByID,
	getAllSocialInfo,
	upPicture
} from '../../api/api.js';

export default {
	data() {
		return {
			userInfo: '',
			socialInfo: '',
			userId: uni.getStorageSync('USERS_KEY').id,
			avatar: '../../static/img/user/upload.svg',
			settings: [{
				src: '../../static/img/user/relNameAuth.svg',
				url: '../auth/auth',
				label: '實名認證',
				expla: 'PAN幣綫上交易所交易需要實名認證'
			}, {
				src: '../../static/img/user/invCode.svg',
				url: '../invite/invite',
				label: '我的邀請碼',
				expla: '獲得PAN幣和原力'
			}, {
				src: '../../static/img/user/assetLedger.svg',
				url: '../ledger/asset',
				label: '資產賬本',
				expla: ''
			}, {
				src: '../../static/img/user/focusLedger.svg',
				url: '../ledger/focus',
				label: '原力賬本',
				expla: ''
			}, {
				src: '../../static/img/user/plantBase.svg',
				url: '../base/base',
				label: '招募節點',
				expla: ''
			}, {
				src: '../../static/img/user/option.svg',
				url: '../setting/setting',
				label: '設置',
				expla: ''
			}]
		}
	},
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	methods: {
		...mapMutations(['logout']),
		bindLogout() {
			let _this = this;
			uni.showModal({
				title: '',
				content: '點擊確定後退出',
				success(res) {
					if (res.confirm) {
						_this.logout();
						uni.clearStorageSync('USERS_KEY');
						uni.reLaunch({
							url: '../login/login'
						});
					}
				}
			});

		},
		getUserInfo() {
			findByID().then(data => {
				this.userInfo = data;
				this.avatar = data.portrait;
				if (this.userInfo.signature == null || this.userInfo.signature == '') {
					this.userInfo.signature = '這家夥很懶，還沒有簽名';
				}
			});
		},
		getSocialInfo() {
			getAllSocialInfo().then(data => {
				this.socialInfo = data;
			});
		},
		uploadAvatar() {
			upPicture(this.userId).then(data => {
				this.avatar = data.id;
			});
		},
		touserInfo(id) {
			uni.navigateTo({
				url: '/pages/daily/userInfo?uid=' + id
			});
		}
	},
	onShow() {
		this.getUserInfo();
		this.getSocialInfo();
	},
	godynm() {

	}
}
